/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 1.5rem;
  background-color: #0d192b;
  color: #000000;
  line-height: 1.3;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 95vh;
}

main {
  display: flex;
  flex-flow: column;
  padding: 1.5rem;
  background-color: #14253d;
  border-radius: 15px;
  max-width: 350px;
  max-height: 600px;
  box-shadow: 0px 10px 30px 0px #090d1e;
  overflow: hidden;
}
main .nft .NftImageContainer {
  position: relative;
  overflow: hidden;
  margin: 0px;
  padding: 0px;
  transition-duration: 0.5S;
}
main .nft .NftImageContainer .NftImage {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
main .nft .NftImageContainer::before {
  content: "";
  background-color: #00fff7;
  opacity: 0;
  width: 100%;
  height: 99%;
  position: absolute;
  border-radius: 10px;
  transition-duration: 0.5S;
  cursor: pointer;
}
main .nft .NftImageContainer::after {
  opacity: 0;
  border-radius: 10px;
  content: url(../../images/icon-view.svg);
  height: 99%;
  position: absolute;
  width: 100%;
  left: 42%;
  top: 42%;
  transition-duration: 0.5S;
  cursor: pointer;
}
main .nft .NftImageContainer:hover::before {
  content: "";
  background-color: #00fff7;
  opacity: 0.5;
  width: 100%;
  height: 99%;
  position: absolute;
  border-radius: 10px;
  transition-duration: 0.5S;
  cursor: pointer;
}
main .nft .NftImageContainer:hover::after {
  border-radius: 10px;
  content: url(../../images/icon-view.svg);
  height: 99%;
  position: absolute;
  width: 100%;
  left: 42%;
  top: 42%;
  transition-duration: 0.5S;
  cursor: pointer;
  opacity: 1;
}
main .nft .NftTitle {
  margin: 0%;
  margin-top: 1rem;
  transition-duration: 0.5s;
}
main .nft .NftTitle:hover {
  color: #00fff7;
  cursor: pointer;
  transition-duration: 0.5s;
}
main .nft .NftDescription {
  margin: 0%;
  margin-top: 0.875rem;
  font-weight: 300;
  color: #8bacda;
}
@media only screen and (max-width: 620px) {
  main .nft .NftDescription {
    font-size: 1.04rem;
  }
}
main .nft .NftInfo {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;
}
main .nft .NftInfo .NftPrice {
  display: flex;
  align-items: center;
}
main .nft .NftInfo .NftPrice .PriceEth {
  margin: 0px 0.5rem;
  color: #00fff7;
  font-weight: 600;
  font-size: 1rem;
}
main .nft .NftInfo .NftDuration {
  display: flex;
  align-items: center;
}
main .nft .NftInfo .NftDuration .TimeDuration {
  margin: 0px;
  margin-left: 0.5rem;
  color: #8bacda;
}
main .creator {
  margin-top: 1.5rem;
  border-top: 1px solid #2f415b;
  display: flex;
  align-items: center;
  padding-top: 0.5rem;
}
main .creator .profile {
  height: 34px;
  border: 2px solid #ffffff;
  border-radius: 20rem;
}
main .creator p {
  margin-left: 1rem;
  color: #8bacda;
  font-weight: 400;
  font-size: 1rem;
}
main .creator .NftCreator {
  margin-left: 0.3rem;
  color: #ffffff;
  transition-duration: 0.5s;
}
main .creator .NftCreator:hover {
  color: #00fff7;
  cursor: pointer;
  transition-duration: 0.5s;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
  color: #000000;
}

p {
  font-size: 1.125rem;
}

body {
  font-family: "Outfit", sans-serif;
  color: #ffffff;
}